  border-radius: 4px;
  margin-left: 5px;
  padding: 5px 10px;
}

#reminder-items li button:hover {
  background-color: #0056b3;
}

.popup {
  background-color: #00000080;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
